<template>
  <nav class="navbar fixed-top navbar-expand-lg navbar-dark bg-slate">
    <div class="container">
        <!-- Left Content -->
        <a class="navbar-brand" href="#">CLOUDS icon
            <!-- <img src="/docs/5.0/assets/brand/bootstrap-logo.svg" alt="" width="30" height="24"> -->
        </a>
        <!-- Right Content -->
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <transition-group tag="ul" appear class="navbar-nav ms-auto" @before-enter="beforeEnter" @enter="enter">
                <li v-for="(name, index) in navLinks" :key="name.name" :data-index="index" class="nav-item">
                     <a class="nav-link active fs-5" aria-current="page">
                        <router-link class="link" :to="name.to">
                            {{ name.text }}
                        </router-link>
                    </a>
                </li>
            </transition-group>
        </div>
    </div>
  </nav>
</template>

<script>
import { ref } from 'vue'
import gsap from 'gsap'

export default {
    setup() {
        const navLinks = ref([
            {
                name: 'Home',
                text: 'Home',
                to: '/'
            },
            {
                name: 'Career',
                text: 'Career',
                to: 'career'
            },
            {
                name: 'About',
                text: 'About',
                to: '/#contact'
            },
        ])
        const beforeEnter = (el) => {
            // console.log('before enter')
            el.style.opacity = 0;
            el.style.transform = 'translateX(-25px)'
        }
        const enter = (el, done) => {
            gsap.to(el, {
                opacity: 1,
                x: 0,
                duration: 0.8,
                onComplete: done,
                delay: 0.1
            })
        }
        return { navLinks, beforeEnter, enter }
    }

}
</script>

<style scoped>
.bg-slate {
    background-color: #2B2F33;
    opacity: 0.8;
}

.link {
    color: white;
    text-decoration: none;
}

.link:hover {
    color: cornflowerblue;
    text-decoration: underline;
}
</style>