<template>
  <div class="container-fluid">
      <div class="row justify-content-center">
          <div class="col-md-6 pb-3" v-for="(circle, i) in circles" :key="i">
            <GoogleMap
                api-key="AIzaSyBBc1fOOSIFiJuHaP6hbk9XzyzLu4RWfwo"
                style="width: 100%; height: 500px"
                :center="circle.center"
                :zoom="5"
                >
                    <Circle :options="circle" />
            </GoogleMap>
          </div>
                    
      </div>
  </div>
</template>

<script>
// AIzaSyBBc1fOOSIFiJuHaP6hbk9XzyzLu4RWfwo
import { defineComponent } from 'vue'
import { GoogleMap, Circle } from 'vue3-google-map'


export default defineComponent({
    name: 'contact',
    components: { GoogleMap, Circle },
    setup() {
        const center = { lat: 37.549863, lng: -122.304932 }
        const cities = {
            sf: {
                center: { lat: 37.549863, lng: -122.304932 },
                population: 2714856,
            },
            ny: {
                center: { lat: 40.714, lng: -74.005 },
                population: 8405837,
            },
            
        }
        const circles = {}

        for (const key in cities) {
            circles[key] = {
                center: cities[key].center,
                radius: Math.sqrt(cities[key].population) * 100,
                strokeColor: '#FF0000',
                strokeOpacity: 0.8,
                strokeWeight: 2,
                fillColor: '#FF0000',
                fillOpacity: 0.35,
            }
        }
        return { center, circles }
    }
})
</script>

